<template>
  <div class="topCenter list_box">
    <div id="topCenter"></div>
  </div>
</template>

<script>
export default {
  name: "lineChart",
  data() {
    return {
      option: {},
    };
  },
  methods: {
    createData() {
      var chartDom = document.getElementById("topCenter");
      var myChart = this.$echarts.init(chartDom);
      var option;
      option = {
        title: {
          text: "公司与外租用电对比",
          left: "center",
          textStyle: {
            color: "#fff",
            fontWeight: "normal",
          },
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          show: true,
          data: ["外租", "公司"],
          bottom:15,
          textStyle:{
            color:'#fff'
          }
        },
        grid: {
          top: "15%",
          left: "10%",
          right: "5%",
           bottom: "19%",
        },
        xAxis: {
         data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月",
            ],
          boundaryGap: false,
          axisLine: {
            //坐标轴颜色
            show: true,
            lineStyle: {
              color: "#5b73ffb0",
            },
          },
          axisLabel: {
            //坐标轴上文字颜色
            interval: 0,
            color: "#fff",
          },
          axisTick: {
            //坐标轴刻度线
            show: false,
          },
        },
        yAxis: {
          name: "(kWh)",
          max: function (value) {
            return value.max * 1.2;
          },
          nameTextStyle: {
            color: "#fff",
            fontSize: 12,
          },
          type: "value",
          splitLine: {
            show: false, //去掉网格线
          },

          axisLine: {
            //坐标轴颜色
            show: true,
            lineStyle: {
              color: "#5b73ffb0",
            },
          },
          axisLabel: {
            //坐标轴上文字颜色
            color: "#fff",
          },
        },
        series: [
          {
           data: [120, 132, 101, 134, 90, 230, 210,180,150,240,210,200],
            smooth: true,
            name:"外租",
            type: "line",
            lineStyle: {
              width: 1,
              color: "#d6f478",
            },
            itemStyle: {
              normal: {
                color: "#d6f478",
              },
            },
          },
          {
           data: [150, 232, 201, 154, 190, 330, 410,150, 232, 201, 154, 190],
            smooth: true,
            name:"公司",
            type: "line",
           lineStyle: {
              width: 1,
              color: "#ff0000",
            },
            itemStyle: {
              normal: {
                color: "#ff0000",
              },
            },
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData();
  },
};
</script>

<style lang="less" scoped>
#topCenter {
  width: 100%;
  height: 100%;
}
.topCenter {
  width: 34%;
  padding-top: 20px;
}
</style>
